import { BASE_API } from '../../config/prod.env'
export default {
  data () {
    return {
      info: {
        qrcode: '',
        name: '',
        id: ''
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '使用帮助'
    var info = JSON.parse(localStorage.getItem('businessNeed'))
    next(vm => {
      vm.info = info
    })
  },
  filters: {
    screenUrl: (val) => {
      return BASE_API + '/screen/?ht_id=' + val
    }
  }
}



